import React, { Component } from "react";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";
import { createNumberMask } from "redux-form-input-masks";
import { getTimbangan } from "../../GetTimbangan";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { setFocus, SkeletonLoading } from "../../helper";
import { getListBarang } from "../../actions/kodeBarangAction";
import { warning } from "../../Alert";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormPengambilanTimbangan extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "INI KETERANGAN",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
      kadarCetak: "",
      kadarBeli: "",
      namaJenis: "",
      kodeJenis: "",
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    getTimbangan(this.props.change);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("pkg", this.props.pkg);
  }

  pilihBarang(data) {
    let param = data;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    let customer = localStorage.getItem("customer_1") || "-";
    AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer}&${data}&PUSAT`
    ).then((res) => {
      if (
        res.data?.kode_tukar_normal === undefined ||
        res.data?.kode_tukar_normal === "undefined"
      ) {
        warning(
          "Kode Tukar Belum Ditentukan ,Mohon tentukan Kode Tukar di menu kelompok harga"
        );
        this.props.change("kodeBarang");
        return false;
      }
      if (res.data?.kode_kelompok === "AKSESORIS") {
        this.setState({
          non_aksesoris: "col-lg-12 row d-none",
          aksesoris: "col-lg-12 row",
        });
      } else {
        this.setState({
          non_aksesoris: "col-lg-12 row",
          aksesoris: "col-lg-12 row d-none",
        });
      }
      this.setState({
        kodeJenis: hasil[0].kode_jenis,
        namaJenis: hasil[0].nama_jenis,
        kadarCetak: hasil[0].kadar_cetak,
        kadarJual: res.data?.kode_tukar_normal,
        kodeKategori: res.data?.kode_kategori,
        namaKategori: res.data?.nama_kategori,
        harga_jual: res.data?.harga_aksesoris || 0,
      });
      this.setKadar();
      setFocus("Berat (Gr)");
    });
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
    this.props.change("kode_jenis", this.state.kodeJenis);
    this.props.change("nama_jenis", this.state.namaJenis);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 form-group">
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                label="Kode Model"
                name="kodeJenis"
                component={renderFieldSelect}
                options={this.props.KodeBarangList.filter(
                  (fil) => fil.kode_kelompok === "PERHIASAN"
                ).map((list) => {
                  let data = {
                    value: list.kode_jenis,
                    name: list.nama_jenis,
                  };
                  return data;
                })}
                onChange={(data) => this.pilihBarang(data)}
                onBlur={this.setKadar()}
                placeholder="Silahkan Pilih"
              ></Field>
            ) : (
              <SkeletonLoading label="Kode Model" />
            )}
          </div>
          <div className="col-md-8 form-group">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>

          <div className="col-md-3 form-group">
            <Field
              name="kadar"
              label="Cap Kode"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group d-none">
            <Field
              name="kode_jenis"
              label="Kode Tukar"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group d-none">
            <Field
              name="nama_jenis"
              label="Kode Tukar"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group d-none">
            <Field
              name="kadar_jual"
              label="Kode Tukar"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          {/* <div className="col-md-3 form-group">
            <Field name="p" label="Plastik" type="number" component={renderField} />
          </div>
          <div className="col-md-3 form-group">
            <Field name="k" label="Kertas" type="number" component={renderField} />
          </div> */}
          <div className="col-md-3 form-group">
            <Field
              name="pkg"
              label="Bungkus (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
              readOnly
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="bruto"
              id="bruto"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadarTransaksi"
              label="Kode Tukar"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="gross"
              label="Gross (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="netto"
              label="Murni (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormPengambilanTimbangan = reduxForm({
  form: "TerimaSupplier",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormPengambilanTimbangan);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi, gross } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "gross"
  );
  return {
    KodeBarangList: state.provinsi.kodeBarang,
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    pkg: (parseFloat(gross) - parseFloat(bruto)).toFixed(3),
  };
})(FormPengambilanTimbangan);
